import { mapMutations, mapActions } from 'vuex'
import { upyunUrl } from '../../../api/config'
import QualityList from '../../../components/subjectQualityListTemplate'

export default {
  data () {
    return {
      upyunUrl,
      obj: '',
      oJson: ''
    }
  },
  onLoad (options) {
    this.obj = JSON.parse(options.oJson)
    uni.setNavigationBarTitle({
      title: this.obj.title
    })
    this.getSubjectGoodsListFn()
  },
  // 下拉刷新
  onPullDownRefresh () {
    this.getSubjectGoodsListFn()
  },
  methods: {
    ...mapMutations({
    }),
    ...mapActions([
      'getSubjectGoodsList'
    ]),
    // 获取精品课程列表
    getSubjectGoodsListFn () {
      const data = {
        that: this,
        collection_id: this.obj.id,
        category_id: ''
      }
      uni.showLoading({ title: 'Loading' })
      this.getSubjectGoodsList(data).then((res) => {
        this.oJson = res[0]
        uni.stopPullDownRefresh()
        uni.hideLoading()
      })
    }
  },
  components: {
    QualityList
  }
}
